import React, { useState, useEffect, forwardRef } from 'react';
import axios from 'axios';
import Papa from 'papaparse';
import Table from '@mui/material/Table';
import TableBody from '@mui/material/TableBody';
import TableCell from '@mui/material/TableCell';
import TableContainer from '@mui/material/TableContainer';
import TableHead from '@mui/material/TableHead';
import TableRow from '@mui/material/TableRow';
import Paper from '@mui/material/Paper';
import { TableVirtuoso } from 'react-virtuoso';

const VirtuosoTableComponents = {
  Scroller: forwardRef((props, ref) => (
    <TableContainer component={Paper} {...props} ref={ref} />
  )),
  Table: (props) => (
    <Table {...props} sx={{ borderCollapse: 'separate', tableLayout: 'fixed' }} />
  ),
  TableHead: forwardRef((props, ref) => <TableHead {...props} ref={ref} />),
  TableRow,
  TableBody: forwardRef((props, ref) => <TableBody {...props} ref={ref} />),
};

export default function VirtualWinnerTable() {
  const [rows, setRows] = useState([]);
  const [columns, setColumns] = useState([]);
  const [loading, setLoading] = useState(true);
  let data = null;
  useEffect(() => {

    const intervalId = setInterval(() => {
      const fetchData = async () => {
        try {
          const response = await axios.get(
            'https://docs.google.com/spreadsheets/d/e/2PACX-1vSKusOsGr1tyRSPIoDIg33gMsPLfMI5KZSzDjE_xMYV5fdrfqhQO4JMRQD0lqDVdDIssYXp1UnGit7h/pub?gid=2022149819&single=true&output=csv'
          );
          let file = response.data;
          if (file) {
            Papa.parse(file, {
              header: true,
              dynamicTyping: true,
              complete: (results) => {
                data = results.data;
                if (data.length > 0) {
                  let parsedColumns = Object.keys(data[0])
                    .map((key) => ({
                      field: key,
                      headerName: key,
                      width: 150,
                    }))
                    .filter((col) => {
                      if (col.field === "We need to identify you for the leaderboard and prizes. ") {
                        col.headerName = "Name";
                        col.width = 500;
                        return col;
                      }
                      if (col.field === "score") {
                        col.headerName = "Score";
                        return col;
                      }
                    });
                  parsedColumns.unshift({ field:"Rank" , headerName: "Rank", 
                  width: 100})
                  setColumns(parsedColumns);
                  setRows(data.map((row, index) => ({ key: index, ...row, Rank: calculateRank(row.score)  })).sort((a, b) => b.score - a.score));
                  setLoading(false);
                }
              },
            });
          }
        } catch (error) {
          console.error('Error fetching data:', error);
          setLoading(false);
        }
      };
      fetchData();
    }, 10000);
 

    
  }, []);

  const fixedHeaderContent = () => (
    <TableRow>
      {columns.map((column) => (
        <TableCell
          key={`${column.field}`}
          variant="head"
          align={'left'}
          style={{ width: column.width }}
          sx={{
            backgroundColor: 'background.paper',
          }}
        >
          {column.headerName}
        </TableCell>
      ))}
    </TableRow>
  );

  const rowContent = (_index, row) => (
    <>
      {columns.map((column) => (
        <TableCell key={`${row.id}-${column.field}`} align={'left'}>
          {row[column.field]}
        </TableCell>
      ))}
    </>
  );
  const calculateRank = (score) => {
    const sortedScores = data.map((row) => row.score).sort((a, b) => b - a);
    const rank = sortedScores.indexOf(score) + 1;
    return rank;
    // Example logic: Higher scores get higher ranks
   
  };

  if (rows.length === 0) {
    return <div style={{display: 'grid', justifyContent: 'center'}}>No data available</div>;
  }

  return (
    <Paper style={{ height: '99%', width: '100%' }}>
      {loading ? (
        <div>Loading...</div>
      ) : (
        <TableVirtuoso
          data={rows}
          components={VirtuosoTableComponents}
          fixedHeaderContent={fixedHeaderContent}
          itemContent={rowContent}
        />
      )}
    </Paper>
  );
}
